<template>
	<div>
		<div class="input_box">
			<el-input v-model="input" @change="inputChange"></el-input>
		</div>
		<div class="myChart" id="myChart1"></div>
		<div class="myChart" id="myChart2"></div>
		<div class="myChart" id="myChart3"></div>
		<div class="myChart" id="myChart4"></div>
		<div class="myChart" id="myChart5"></div>
	</div>
</template>

<script>
	import * as echarts from 'echarts';
	export default {
		data() {
			return {
				input: '',
				myChart1: null,
				myChart2: null,
				myChart3: null,
				myChart4: null,
				myChart5: null,
				options: {
					title: {},
					tooltip: {
						trigger: 'axis',
						axisPointer: {
							type: 'cross'
						}
					},
					legend: {
						show: false
					},
					grid: {
						left: '1%',
						right: '3%',
						bottom: '3%',
						containLabel: true
					},

					// dataZoom: [{
					// 	show: true,
					// 	realtime: true,
					// 	start: 90,
					// 	end: 100
					// }],
					xAxis: [{
						data: [],
					}],
					yAxis: [{
						interval: 1,
						min: 0,
						max: 9,
					}],
					series: [{
						name: '和',
						type: 'line',
						yAxisIndex: 0,
						label: {
							show: true,
							color: '#EE6666',
							position: 'top'
						},
						data: [],
						markLine: {
							symbol: ['none', 'none'],
							data: [{
								yAxis: 4.5,
							}]
						},
					}]
				},
				originalData: [],
			}
		},
		mounted() {
			this.myChart1 = echarts.init(document.getElementById('myChart1'))
			this.myChart2 = echarts.init(document.getElementById('myChart2'))
			this.myChart3 = echarts.init(document.getElementById('myChart3'))
			this.myChart4 = echarts.init(document.getElementById('myChart4'))
			this.myChart5 = echarts.init(document.getElementById('myChart5'))

			let always = sessionStorage.getItem('always')
			if (always) this.originalData = JSON.parse(always)
		},
		watch: {
			originalData(newValue) {
				let xData = []
				let yData1 = []
				let yData2 = []
				let yData3 = []
				let yData4 = []
				let yData5 = []
				newValue.forEach((item, index) => {
					xData.push(index)
					yData1.push(item.split('')[0])
					yData2.push(item.split('')[1])
					yData3.push(item.split('')[2])
					yData4.push(item.split('')[3])
					yData5.push(item.split('')[4])
				})

				this.options.xAxis[0].data = xData
				let options1 = JSON.parse(JSON.stringify(this.options))
				options1.series[0].data = yData1
				this.myChart1.setOption(options1)

				let options2 = JSON.parse(JSON.stringify(this.options))
				options2.series[0].data = yData2
				this.myChart2.setOption(options2)

				let options3 = JSON.parse(JSON.stringify(this.options))
				options3.series[0].data = yData3
				this.myChart3.setOption(options3)

				let options4 = JSON.parse(JSON.stringify(this.options))
				options4.series[0].data = yData4
				this.myChart4.setOption(options4)

				let options5 = JSON.parse(JSON.stringify(this.options))
				options5.series[0].data = yData5
				this.myChart5.setOption(options5)
			}
		},
		methods: {
			inputChange() {
				if (this.input.length == 5) {
					this.originalData.push(this.input)
					sessionStorage.setItem('always', JSON.stringify(this.originalData))
					this.input = ''
				} else {
					this.$message.warning('5个数字')
				}
			},
		}
	}
</script>

<style scoped>
	.input_box {
		position: fixed;
		top: 10px;
		right: 100px;
		z-index: 10000;
	}

	.myChart {
		width: 99vw;
		height: 19vh;
	}
</style>